<template lang="html">
  <!-- gotoRouter:idroute.name -->
  <footer class="footer" @click.stop='gotoRouter'>
    <mt-tabbar v-model="selected" fixed>
    <mt-tab-item id="">
      <i slot="icon" class="icon-index"></i>
        
    </mt-tab-item>
    <mt-tab-item id="">
      <i slot="icon" class="icon-category"></i>
      
    </mt-tab-item>
    <mt-tab-item id="">
      <i slot="icon" class="icon-car"></i>
      
    </mt-tab-item>
    <mt-tab-item id="">
      <i slot="icon" class="icon-user"></i>
      
    </mt-tab-item>
  </mt-tabbar>
  </footer>
</template>

<script>
  export default {
    methods: {
      gotoRouter() {
        this.$router.push({
          name: this.selected
        })
      }
    },
    data() {
      return {
        //mt-tab-item id
        selected: ''
      }
    },
watch:{
    '$route':function(to,from){
        //this.routeName = to.name;
        this.$parent.$emit("changeTab");
    }
},
    mounted() {

      this.$parent.$on("changeTab", function()
      {
          //footeris-selected
          let Rname = this.$route.name;
          switch (Rname) {
            case '':
              this.selected = '';
              break;
            case '':
              this.selected = '';

              break;
            case '':
              this.selected = '';

              break;
            case '':
              this.selected = '';
              break;
            case '':
              this.selected = '';
              break;
          }
      }.bind(this));

      this.$parent.$emit("changeTab");

    }

  }
</script>

<style lang="less" scoped>
  @import '../assets/fz.less';
  @import '../assets/index/style.css';

.mint-tab-item-label:hover {
  color:#333;

}
  .footer {


    .mint-tabbar {height: 55px;
      background-color: #fff;
      background-image: none;
      box-shadow: 0 0 2.2vw 0 hsla(0, 6%, 50%, .13);
      -webkit-box-shadow: 0 0 2.2vw 0 hsla(0, 6%, 50%, .13);
      .is-selected {
        color: @cl;
        background-color: #fff;
        i {
          &::before {
            color: @cl;
          }
        }
      }
      i {
        .fz(font-size, 42);
      }
    }
  }
</style>



